import styled from "styled-components";
import { useContext } from "react";
import RouletteContext from "../context/RouletteContext";
import { MdRemove } from "react-icons/md";

const Remove = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  color: gray;
  font-size: 20px;
  cursor: pointer;
  display: none;
`;

const CandidateItemBlock = styled.div`
  padding: 5%;
  padding-top: 5px;
  padding-bottom: 5px;

  display: flex;
  &:hover {
    text-shadow: 0px 0px 1.5px gray;
    ${Remove} {
      display: initial;
    }
  }
`;

const Text = styled.div`
  font-family: sans-serif;
  font-weight: bold;
  font-size: 20px;
  padding-right: 10px;
`;
const CandidateItem = ({ id, text }) => {
  const { state, dispatch, nextId } = useContext(RouletteContext);

  const onRemove = () => {
    dispatch({
      type: "REMOVE",
      payload: { id },
    });
    if (state.length === 1) {
      nextId.current = 1;
    }
  };

  return (
    <CandidateItemBlock>
      <Text onClick={onRemove}> {text}</Text>
      <Remove onClick={onRemove}>
        <></>Click to remove
      </Remove>
    </CandidateItemBlock>
  );
};

export default CandidateItem;
