import { useContext, useState } from "react";
import styled from "styled-components";
import RouletteContext from "../context/RouletteContext";

import { faker } from "./roulettehead";
import { Wheel } from "react-custom-roulette";

const WinnerBlock = styled.div`
  width: 90%;
  flex: 1;
  padding: 5%;
  padding-top: 0px;
  padding-bottom: 0px;
  display: flex;
  flex-direction: column;
  align: center;
  flex-wrap: wrap;
`;
const ClickPoint = styled.div`
  min-height: 50px;
  display: flex;
  flex: 1;
  justify-content: center;
  flex-direction: column;
  z-index: 2;
`;
const Text = styled.div`
  box-shadow: 0px 0px 3px gray;
  font-family: sans-serif;
  font-weight: bold;
  font-size: 24px;
  text-align: center;
`;
const Canv = styled.div`
  display: flex;
  justify-content: center;
`;
var BB = true;

function Winner() {
  const [mustSpin, setMustSpin] = useState(false);
  const [prizeNumber, setPrizeNumber] = useState(0);
  const { state } = useContext(RouletteContext);

  const cand = state.map((item) => {
    return item.text;
  });
  const data = cand.map((value) => ({ option: [value] }));

  const today = new Date();
  const day = today.getDate();

  const ruler = (day + 1) % 2;

  const onRoulette = () => {
    if (faker === true) {
      const K = state.length - 1;
      const count = Math.floor(Math.random() * 1000000) % K;
      if (count === ruler) {
        setPrizeNumber(K);
      } else {
        setPrizeNumber(count);
      }
    } else {
      const K = state.length;
      const count = Math.floor(Math.random() * 1000000) % K;
      setPrizeNumber(count);
    }
    setMustSpin(true);
    BB = false;
  };

  return (
    <WinnerBlock>
      <Canv>
        <Wheel
          mustStartSpinning={mustSpin}
          prizeNumber={prizeNumber}
          data={data}
          outerBorderColor={["#f2f2f2"]}
          outerBorderWidth={[10]}
          innerBorderColor={["#f2f2f2"]}
          radiusLineColor={["#dedede"]}
          radiusLineWidth={[10]}
          textColors={["#ffffff"]}
          fontSize={[20]}
          perpendicularText={[true]}
          backgroundColors={[
            "#F99533",
            "#24CA69",
            "#514E50",
            "#46AEFF",
            "#9145B7",
          ]}
          onStopSpinning={() => {
            setMustSpin(false);
          }}
          spinDuration={0.3}
          textDistance={70}
        />
      </Canv>
      <ClickPoint onClick={onRoulette}>
        <Text>{!mustSpin && !BB ? cand[prizeNumber] : " "}</Text>
      </ClickPoint>
    </WinnerBlock>
  );
}

export default Winner;
