import { useContext, useState } from "react";
import styled from "styled-components";
import { css } from "styled-components";
import FakerContext from "../context/FakerContext";
import RouletteContext from "../context/RouletteContext";
import { faker } from "./roulettehead";

const ButtonsBlock = styled.div`
  width: 100%;
  height: 10%;
  flex: 1;
  padding: 0%;
  padding-top: 0px;
  padding-bottom: 0px;

  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
`;

const AddButton = styled.div`
  min-height: 50px;
  display: flex;
  flex: 1;
  text-align: center;
  align-items: center;
  background: #0cad0f;
  height: 100%;
  border-radius: 16px;
  box-shadow: 0 0 8px 0 rbga(0, 0, 0, 0.04);
  justify-content: center;
  z-index: 1;
`;

const FastButton = styled.div`
   {
    min-height: 50px;
    display: flex;
    flex: 1;
    text-align: center;
    align-items: center;
    background: #d48020;
    height: 100%;
    border-radius: 16px;
    box-shadow: 0 0 8px 0 rbga(0, 0, 0, 0.04);
    justify-content: center;
    z-index: 1;
    ${(props) =>
      props.fake &&
      css`
        background: orange;
      `}
  }
`;

const Text = styled.div`
  font-family: sans-serif;
  font-weight: bold;
  font-size: 30px;
  text-align: center;
  color: white;
`;

const InsertFormPositioner = styled.div`
  width: 100%;
  bottom: 10%;
  left: 0;
  position: absolute;
  z-index: 3;
`;

const InsertForm = styled.form`
  background: #f8f9fa;
  padding: 5%;
  padding-top: 1vh;
  padding-bottom: 1vh;

  border-bottom-left-radius: 16px;
  border-bottom-right-radius: 16px;
  border-top: 1px solid #e9ecef;
`;

const Input = styled.input`
  padding: 12px;
  border-radius: 4px;
  border: 1px solid #dee2e6;
  width: 100%;
  outline: none;
  font-size: 18px;
  box-sizing: border-box;
`;

const Buttons = () => {
  const [open, setOpen] = useState(false);
  const onToggle = () => setOpen(!open);
  const { state, dispatch, nextId } = useContext(RouletteContext);
  const [value, setValue] = useState("");

  const onChange = (e) => setValue(e.target.value);
  const onSubmit = (e) => {
    e.preventDefault();

    dispatch({
      type: "ADD",
      payload: {
        id: nextId.current,
        text: value,
      },
    });

    setValue("");
    setOpen(false);
    nextId.current++;
  };
  const onFast = () => {
    dispatch({
      type: "ADD",
      payload: {
        id: nextId.current,
        text: nextId.current + " ",
      },
    });
    nextId.current++;
  };
  return (
    <>
      {open && (
        <InsertFormPositioner>
          <InsertForm onSubmit={onSubmit}>
            <Input
              autoFocus
              placeholder=" "
              onChange={onChange}
              value={value}
            />
          </InsertForm>
        </InsertFormPositioner>
      )}
      <ButtonsBlock>
        <svg width="1px"></svg>
        <AddButton onClick={onToggle} open={open}>
          <Text></Text>
        </AddButton>
        <svg width="1px"></svg>
        <FastButton onClick={onFast} fake={faker}>
          <Text></Text>
        </FastButton>
        <svg width="1px"></svg>
      </ButtonsBlock>
    </>
  );
};

export default Buttons;
