import { useContext } from "react";
import styled from "styled-components";
import RouletteContext from "../context/RouletteContext";
import CandidateItem from "./candidateItem";

const CandidateBlock = styled.div`
  min-width: 30vmin;
  max-height: 40vh;
  flex: 1;
  padding: 5%;
  padding-top: 0px;
  padding-bottom: 0px;

  display: flex;
  flex-direction: column;
`;

function Candidate() {
  const { state } = useContext(RouletteContext);

  return (
    <CandidateBlock>
      {state.map((item) => {
        return (
          <CandidateItem
            key={item.id}
            text={item.text}
            faker={item.faker}
            id={item.id}
          ></CandidateItem>
        );
      })}
    </CandidateBlock>
  );
}

export default Candidate;
