import { useContext, useState } from "react";
import styled from "styled-components";
import FakerContext from "../context/FakerContext";

const RouletteHeadBlock = styled.div`
  width: 90%;
  flex: 0.5;

  padding: 5%;
  padding-top: 0px;
  padding-bottom: 0px;

  text-align: center;

  h1 {
    margin: 5px;
    text-align: center;
    align-items: center;
    justify-content: center;
    color: #343a40;
    display: flex;
    flex-direction: column;
  }
`;
var faker = false;
function RouletteHead() {
  const fakeMode = () => {
    faker = !faker;
  };
  const today = new Date();
  const dateString = today.toLocaleDateString("ko-KR", {
    year: "numeric",
    month: "long",
    day: "numeric",
  });
  return (
    <RouletteHeadBlock>
      <h1 onClick={fakeMode}>{dateString}</h1>
    </RouletteHeadBlock>
  );
}

export default RouletteHead;
export { faker };
