import "./App.css";
import { createGlobalStyle } from "styled-components";
import RouletteTemplate from "./component/RouletteTemplate";
import RouletteHead from "./component/roulettehead";
import Winner from "./component/winner";
import Candidate from "./component/candidate";
import Buttons from "./component/buttons";
import { useReducer, useRef } from "react";
import RouletteContext from "./context/RouletteContext";
import { getInitialState, RouletteReducer } from "./component/RouletteReducer";
import FakerContext from "./context/FakerContext";
import Container from "./component/container";

const GlobalStyle = createGlobalStyle`
  body {
    background-color: #ECF6CE;
  }
`;

function App() {
  const [state, dispatch] = useReducer(RouletteReducer, getInitialState());
  const nextId = useRef(1);
  var faker = false;
  return (
    <>
      <GlobalStyle />
      <RouletteTemplate>
        <RouletteContext.Provider
          value={{
            state,
            dispatch,
            nextId,
          }}
        >
          <RouletteHead></RouletteHead>
          <Container>
            <Winner></Winner>
            <Candidate></Candidate>
          </Container>
          <Buttons></Buttons>
        </RouletteContext.Provider>
      </RouletteTemplate>
    </>
  );
}

export default App;
