import styled from "styled-components";

const RouletteTemplateBlock = styled.div`
  width: 90%;
  height: 87vh;

  position: relative;
  background: white;
  border-radius: 16px;
  border: 1px solid #e6e7e7;
  box-shadow: 0 0 8px 0 rbga(0, 0, 0, 0.04);

  margin: 0 auto;

  margin-top: 1%;
  margin-bottom: 0%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
`;

const RouletteTemplate = ({ children }) => {
  return <RouletteTemplateBlock>{children}</RouletteTemplateBlock>;
};

export default RouletteTemplate;
