import styled from "styled-components";

const ContainerBlock = styled.div`
  width: 100%;
  flex: 8;
  position: relative;
  background: white;

  margin: 0 auto;

  margin-top: 1%;
  margin-bottom: 0%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-content: flex-start;
`;

const Container = ({ children }) => {
  return <ContainerBlock>{children}</ContainerBlock>;
};

export default Container;
