export const initialState = [];

export const getInitialState = () => {
  return [];
};

const saveItems = (newItems) => {
  localStorage.setItem("items", JSON.stringify(newItems));
};

export const RouletteReducer = (state, action) => {
  switch (action.type) {
    case "ADD": {
      const newItem = action.payload;
      const nextState = state.concat(newItem);

      saveItems(nextState);
      return nextState;
    }
    case "REMOVE": {
      const nextState = state.filter((item) => item.id !== action.payload.id);
      saveItems(nextState);
      return nextState;
    }

    default:
      throw new Error("  action name");
  }
};
